import React from 'react';
import { Form, Input, Button, message } from 'antd';
import { Link } from 'react-router-dom'; // Import Link from react-router-dom
import axios from 'axios';

const LoginPage = () => {
  const onFinish = async (values) => {
    try {
      const response = await axios.post('/login', values);
      message.success(response.data.message);
      // Redirect to dashboard or perform other actions upon successful login
    } catch (error) {
      console.error('Login failed:', error.response.data.message);
      message.error('Login failed. Please check your phone number and password.');
    }
  };
    
      const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
      };
    
      return (
        <div style={{ maxWidth: '300px', margin: 'auto' }}>
          <h2>Đăng nhập</h2>
          <Form
            name="login"
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            initialValues={{ remember: true }}
            layout="horizontal"
          >
            <Form.Item
              label="Số điện thoại"
              name="sdt"
              rules={[{ required: true, message: 'Vui lòng nhập số điện thoại!' }]}
              labelCol={{ span: 8 }} // Điều chỉnh độ rộng của label
              wrapperCol={{ span: 16 }} // Điều chỉnh độ rộng của input
            >
              <Input style={{ width: '100%', maxWidth: '250px' }} />
            </Form.Item>

            <Form.Item
              label="Mật khẩu"
              name="password"
              rules={[{ required: true, message: 'Vui lòng nhập mật khẩu!' }]}
              labelCol={{ span: 8 }} // Điều chỉnh độ rộng của label
              wrapperCol={{ span: 16 }} // Điều chỉnh độ rộng của input
            >
              <Input.Password style={{ width: '100%', maxWidth: '250px', marginLeft: 'auto' }} />
            </Form.Item>
            <div style={{ textAlign: 'center' }}>
              <Link to="/dang-ky">Quên mật khẩu</Link>
            </div>
            <Form.Item>
              <Button type="primary" htmlType="submit" style={{ width: '100%', backgroundColor: '#111467', marginTop: '20px' }}>
                Đăng nhập
              </Button>
            </Form.Item>
          </Form>
          {/* Đường link để chuyển hướng đến trang đăng ký */}
        
        </div>
      );
};

export default LoginPage;
